
@charset "UTF-8";
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
.midea-header {
  padding: 10px;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: white;
  color: #333;
  text-align: center;
}
.midea-header:after {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
    bottom: 0;
    top: auto;
}
.midea-header__hd {
    /*width: 50px;*/
    -webkit-box-flex: 0.5;
    -ms-flex: 0.5;
        flex: 0.5;
    text-align: left;
    color: #0092d8;
}
.midea-header__bd {
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.midea-header__ft {
    text-align: right;
    /*color: #999999;*/
    /*width: 50px;*/
    -webkit-box-flex: 0.5;
    -ms-flex: 0.5;
        flex: 0.5;
}
.midea-header_fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
}

/* 有返回图标 */
.midea-header_back .midea-header__hd {
  position: relative;
  /* < */
}
.midea-header_back .midea-header__hd:before {
    content: '';
    display: inline-block;
    height: 6px;
    width: 6px;
    border-width: 2px 2px 0 0;
    border-color: #0092d8;
    border-style: solid;
    position: relative;
    top: -2px;
    -webkit-transform: rotate(-138deg);
            transform: rotate(-138deg);
    margin-right: 5px;
}



