
@charset "UTF-8";
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}
.midea-loading {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  -webkit-animation: mideaLoading 1s steps(12, end) infinite;
  animation: mideaLoading 1s steps(12, end) infinite;
  background: transparent url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100%;
}
@-webkit-keyframes mideaLoading {
from {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    /* IE 9 */
    -moz-transform: rotate(0deg);
    /* Firefox */
    -webkit-transform: rotate(0deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(0deg);
    /* Opera */
}
to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    /* IE 9 */
    -moz-transform: rotate(360deg);
    /* Firefox */
    -webkit-transform: rotate(360deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(360deg);
    /* Opera */
}
}
@keyframes mideaLoading {
from {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    /* IE 9 */
    -moz-transform: rotate(0deg);
    /* Firefox */
    -webkit-transform: rotate(0deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(0deg);
    /* Opera */
}
to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    /* IE 9 */
    -moz-transform: rotate(360deg);
    /* Firefox */
    -webkit-transform: rotate(360deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(360deg);
    /* Opera */
}
}
.midea-mask {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.6);
}
.midea-dialog {
  background: #fff;
  position: fixed;
  z-index: 5000;
  width: 80%;
  max-width: 300px;
  top: 50%;
  left: 50%;
  border-radius: 5px;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  /* IE 9 */
  -moz-transform: translate(-50%, -50%);
  /* Firefox */
  -webkit-transform: translate(-50%, -50%);
  /* Safari 和 Chrome */
  -o-transform: translate(-50%, -50%);
  /* Opera */
}
.midea-dialog__hd {
    padding: 15px 20px 10px 20px;
    text-align: center;
}
.midea-dialog__hd strong {
      font-weight: 400;
      font-size: 18px;
}
.midea-dialog__bd {
    padding: 0 20px 10px 20px;
    font-size: 15px;
    color: #999;
    word-wrap: break-word;
    word-break: break-all;
}
.midea-dialog__ft {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    line-height: 48px;
    font-size: 18px;
    text-align: center;
    position: relative;
}
.midea-dialog__ft .midea-dialog__btn {
      -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
      display: block;
      text-decoration: none;
      color: #333;
      position: relative;
}
.midea-dialog__ft .midea-dialog__btn:after {
        content: " ";
        position: absolute;
        left: 0;
        top: 0;
        width: 1px;
        bottom: 0;
        border-left: 1px solid #ccc;
        color: #ccc;
        -webkit-transform-origin: 100% 0;
        transform-origin: 100% 0;
        -webkit-transform: scaleX(0.5);
        transform: scaleX(0.5);
}
.midea-dialog__ft .midea-dialog__btn:first-child:after {
        display: none;
}
.midea-dialog__ft .midea-dialog__btn_primary {
        color: #0092d8;
}
.midea-dialog__ft:after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 1px;
      border-top: 1px solid #ccc;
      color: #ccc;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
}



