
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
.midea-tab .midea-navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.midea-tab .midea-navbar__item {
    -webkit-box-flex: 1;
    -ms-flex: 1;
        flex: 1;
    padding: 13px 0;
    text-align: center;
    font-size: 15px;
    position: relative;
}
.midea-tab .midea-navbar__item:after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      width: 1px;
      bottom: 0;
      border-left: 1px solid #ccc;
      color: #ccc;
      -webkit-transform-origin: 100% 0;
      transform-origin: 100% 0;
      -webkit-transform: scaleX(0.5);
      transform: scaleX(0.5);
      left: -1px;
}
.midea-tab .midea-navbar__item:first-child:after {
      display: none;
}
.midea-tab .midea-navbar__item_on {
      background: #EAEAEA;
}
.midea-tab .midea-navbar:after {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #ccc;
    color: #ccc;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
    top: auto;
    bottom: 0;
}



