
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
.midea-tab {
  position: relative;
  height: 100%;
}
.midea-tab__panel {
    height: 100%;
    padding-bottom: 50px;
    overflow: auto;
    box-sizing: border-box;
}
.midea-tab .midea-tabbar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 500;
}
.midea-tab .midea-tabbar__item {
      -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
      text-align: center;
      padding: 5px 0 0;
      font-size: 0;
}
.midea-tab .midea-tabbar__label {
      font-size: 10px;
      text-align: center;
      line-height: 1.8;
      color: #999;
}
.midea-tab .midea-tabbar__icon {
      display: inline-block;
      position: relative;
}
.midea-tab .midea-tabbar__icon img {
        display: inline-block;
        width: 27px;
        height: 27px;
}
.midea-tab .midea-tabbar .midea-badge {
      top: -2px;
      right: -13px;
      position: absolute;
}
.midea-tab .midea-tabbar .midea-badge_dot {
      position: absolute;
      top: 0;
      right: -6px;
}
.midea-tab .midea-tabbar:after {
      content: " ";
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 1px;
      border-top: 1px solid #ccc;
      color: #ccc;
      -webkit-transform-origin: 0 0;
      transform-origin: 0 0;
      -webkit-transform: scaleY(0.5);
      transform: scaleY(0.5);
}
.midea-badge {
  display: inline-block;
  min-width: 8px;
  border-radius: 18px;
  background-color: #f00;
  color: #fff;
  text-align: center;
  font-size: 12px;
  padding: 1px 3px;
  vertical-align: middle;
  line-height: 1.2;
}
.midea-badge_dot {
  padding: 5px;
  min-width: 0;
}



