
@charset "UTF-8";
.hide {
  display: none;
}
.page {
  background: #f8f8f8;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  /* 间距 */
}
.page__hd {
    padding: 40px;
}
.page__bd > .midea-header {
    margin-bottom: 15px;
}
.page__title {
    text-align: left;
    font-size: 20px;
    font-weight: 400;
}
.page__desc {
    margin-top: 5px;
    color: #888;
    text-align: left;
    font-size: 14px;
}
.page.button .page__bd {
  padding: 0 15px;
}
.page.dialog {
  background: #fff;
}
.dialog .page__bd, .toast .page__bd {
  padding: 0 20px;
}



