
@charset "UTF-8";
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}
.midea-loading {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  -webkit-animation: mideaLoading 1s steps(12, end) infinite;
  animation: mideaLoading 1s steps(12, end) infinite;
  background: transparent url(data:image/svg+xml;base64,) no-repeat;
  background-size: 100%;
}
@-webkit-keyframes mideaLoading {
from {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    /* IE 9 */
    -moz-transform: rotate(0deg);
    /* Firefox */
    -webkit-transform: rotate(0deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(0deg);
    /* Opera */
}
to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    /* IE 9 */
    -moz-transform: rotate(360deg);
    /* Firefox */
    -webkit-transform: rotate(360deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(360deg);
    /* Opera */
}
}
@keyframes mideaLoading {
from {
    transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    /* IE 9 */
    -moz-transform: rotate(0deg);
    /* Firefox */
    -webkit-transform: rotate(0deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(0deg);
    /* Opera */
}
to {
    transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    /* IE 9 */
    -moz-transform: rotate(360deg);
    /* Firefox */
    -webkit-transform: rotate(360deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(360deg);
    /* Opera */
}
}
.midea-toast {
  position: fixed;
  z-index: 5000;
  width: 120px;
  min-height: 120px;
  left: 50%;
  top: 35%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  /* IE 9 */
  -moz-transform: translate(-50%, -50%);
  /* Firefox */
  -webkit-transform: translate(-50%, -50%);
  /* Safari 和 Chrome */
  -o-transform: translate(-50%, -50%);
  /* Opera */
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  color: #fff;
  border-radius: 5px;
  position: relative;
}
.midea-toast__content {
    margin: 0 0 15px;
}
.midea-toast__icon {
    width: 38px;
    height: 38px;
    vertical-align: baseline;
    margin: 30px 0 0;
    display: inline-block;
}
.midea-mask_transparent {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /*background: #ccc;*/
}



