
@charset "UTF-8";
.hide {
  display: none;
}
.show {
  display: block;
}

/* Cell Component */
.midea-btn {
  display: block;
  padding-left: 14px;
  padding-right: 14px;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  line-height: 2.56;
  border-radius: 5px;
  box-sizing: border-box;
  color: #fff;
  position: relative;
}
.midea-btn_primary {
    background-color: #0092d8;
}
.midea-btn_default {
    color: #000000;
    background-color: #F8F8F8;
}
.midea-btn_disabled {
    color: rgba(255, 255, 255, 0.6);
    background: #0091d7;
}
.midea-btn_default.midea-btn_disabled {
    color: rgba(0, 0, 0, 0.6);
    background: #F7F7F7;
}
.midea-btn:after {
    content: " ";
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid rgba(0, 0, 0, 0.2);
    /*-webkit-transform: scale(0.5);
   transform: scale(0.5);*/
    transform: scale(0.5);
    -ms-transform: scale(0.5);
    /* IE 9 */
    -moz-transform: scale(0.5);
    /* Firefox */
    -webkit-transform: scale(0.5);
    /* Safari 和 Chrome */
    -o-transform: scale(0.5);
    /* Opera */
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    box-sizing: border-box;
    border-radius: 10px;
}
.midea-btn + .midea-btn {
  margin-top: 15px;
}



